<template>
  <div class="security-container">
    <div class="item-desc">{{ $t('IDC.organizationOperatorDetail.SecurityDesc') }}</div>
    <div class="item-title">{{ $t('IDC.organizationOperatorDetail.Credential') }}</div>
    <table-page
      v-if="isShowTable"
      class="margin-top-16"
      size="small"
      :isCheck="false"
      :isCheckMemory="false"
      :isSingleCheck="false"
      :has-pagination="false"
      :columns="roleColumns"
      :getTableData="getTableData"
      :sortArr="['credentialTypeName', 'credentialStatus', 'expireTime']"
    >
      <template slot-scope="scope" slot="credentialTypeName">
        {{ showPinName(scope.data.row.credentialTypeName) }}
      </template>
      <template slot-scope="scope" slot="credentialStatus">
        <div class="flex flex-align">
          <span class="status-circle" :style="statusStyle(scope.data.row.credentialStatus)"></span>
          <span> {{ showPinStatus(scope.data.row.credentialStatus) }}</span>
        </div>
      </template>
      <template slot-scope="scope" slot="expireTime">
        {{ scope.data.row.expireTime | localTimeFormat('DD/MM/YYYY HH:mm:ss') }}
      </template>
    </table-page>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage';
export default {
  name: 'securityDetailProduct',
  components: { TablePage },
  props: {
    tableData: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      roleColumns: [
        {
          label: this.$t('IDC.organizationOperatorDetail.label.Name'),
          prop: 'credentialTypeName',
          align: 'left',
          __slotName: 'credentialTypeName',
          'sort-method': (a, b) => {
            return a.credentialTypeName > b.credentialTypeName ? 1 : -1;
          },
        },
        {
          label: this.$t('IDC.organizationOperatorDetail.label.Status'),
          prop: 'credentialStatus',
          align: 'left',
          __slotName: 'credentialStatus',
          'sort-method': (a, b) => {
            return a.credentialStatus > b.credentialStatus ? 1 : -1;
          },
        },
        {
          label: this.$t('IDC.organizationOperatorDetail.label.ExpirationTime'),
          prop: 'expireTime',
          align: 'left',
          __slotName: 'expireTime',
          'sort-method': (a, b) => {
            return a.expireTime > b.expireTime ? 1 : -1;
          },
        },
      ],
      isShowTable: true,
    };
  },
  computed: {
    statusStyle() {
      return (status) => {
        switch (status) {
          case '0':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case '1':
            return { 'background-color': '#658DF9' };
          case '3':
            return { 'background-color': `${this.GlobalStyleVariables.statusErrorColor}` };
          case '2':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          default:
            return;
        }
      };
    },
  },
  watch: {
    tableData() {
      this.isShowTable = false;
      this.$nextTick(() => {
        this.isShowTable = true;
      });
    },
    deep: true,
  },
  created() {},
  mounted() {},
  methods: {
    getTableData() {
      return new Promise((resolve) => {
        let res = {};
        res.list = Array.from(this.tableData);
        res.header = {
          success: true,
        };
        resolve(res);
      });
    },
    handleView() {},
    showStatus() {},
    showPinStatus(val) {
      if (!val) {
        return '-';
      }
      switch (val) {
        case '0':
          return 'Pending Change';
        case '1':
          return 'NORMAL';
        case '2':
          return 'Suspended';
        case '3':
          return 'Locked';
        default:
          return '-';
      }
    },
    showPinName(val) {
      if (!val) {
        return '-';
      }
      switch (val) {
        case '01':
          return 'Password';
        case '02':
          return 'PIN';
        case '03':
          return 'Secret word';
        case '04':
          return 'Digital certification';
        case '05':
          return 'Third party password';
        case '06':
          return 'API Password';
        default:
          return '-';
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.security-container {
  .item-title {
    color: $--text_color--strong;
    font-size: 14px;
    margin: 20px 0;
    border-left: 2px solid $--primary_color;
    padding-left: 7px;
  }
  .item-desc {
    color: $--line_color--strong;
    font-size: 12px;
    margin-bottom: 20px;
  }
  /deep/ .el-table--border {
    margin-bottom: 24px;
  }
}
</style>
