<script>
import Role from './Role';
import Security from './Security';
import KycInfo from './kyc-info';
import PortalDialog from '@/components/portal-dialog';
import EditStatus from '../../componts/edit-status.vue';
import { loadLanguageList, queryOrgListByPage } from '@/views/identifier-center/api/api-organization.js';
import { getOrgOperatorKYCDetail } from '@/components/kyc-common/kyc-form/kyc-api.js';
import {
  changeIdentityLanguage,
  orgOperatorDetail,
  changeMsisdn,
  orgOperatorPINRest,
  orgOperatorPINUnlock,
  orgOperatorPINSetPassword,
  getRuleProfile,
  orgResetPassword,
  checkMsisdn,
  getStatusList,
  changeOrgOperatorRuleProfile,
  getMsisdnRule,
  getNotificationTypeList,
  getNotificationTypeInfo,
  sendNotification,
  checkAPIPasswordFormat,
  changeAuthentication,
  getOrganizationList,
} from '@/views/identifier-center/api/api-organization-operator.js';
import {
  inputRequiredValidateObj,
  checkRequiredValidateObj,
  selectRequiredValidateObj,
} from '@/common/validator/common';
import { dateFormat } from '@/common/utils/date-time-utils';
import { STATUS_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';
let ACCESS_CHANNEL = [
  { value: '1005', label: 'Web' },
  { value: '1007', label: 'Handset' },
  { value: '1010', label: 'API' },
];
export default {
  name: 'OrganizationOperatorDetail',
  components: { PortalDialog, KycInfo, Role, Security, EditStatus },
  data() {
    let validateConfirmPassword = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('Please enter'));
      }
      if (value !== this.setPasswordForm.password) {
        return callback(new Error('Please Check Password and Confirm Password!'));
      }
      let params = {
        orgOperatorId: this.receiverIdentityId,
        newPassword: this.setPasswordForm.confirmPassword,
      };
      checkAPIPasswordFormat(params)
        .then((res) => {
          if (res.header && res.header.success) {
            if (res.result) {
              callback();
            } else if (!res.result) {
              let data = res.errorMsg;
              return callback(new Error(JSON.stringify(data)));
            }
          }
        })
        .catch(() => {});
    };
    let getMsisdnRule = (rule, value, callback) => {
      if (!value) {
        return callback();
      }
      let params = {
        msisdn: this.editMSISDNForm.msisdn,
      };
      checkMsisdn(params)
        .then((res) => {
          if (res.header && res.header.success) {
            if (res.flag) {
              callback();
            } else if (!res.flag) {
              let data = res.description;
              return callback(new Error(JSON.stringify(data)));
            }
          }
        })
        .catch(() => {});
    };
    return {
      detail: 'detail',
      identityType: '3000',
      confirmText: this.$t('common.button.submit'),
      submitLoading: false,
      basicInfoForm: {
        accessChannel: [],
      },
      roles: [],
      credentials: [],
      currentPannel: this.$t('IDC.organizationOperatorDetail.label.KYCInfo'),
      demoSelectData: [
        { value: '10', label: 'New CI' },
        { value: '30', label: 'New Version' },
        { value: '60', label: 'Deactive' },
      ],
      editIdentityVisible: false,
      showAuthenticationTypeDialog: false,
      isShowMSISDN: false,
      maxMSISDN: 10,
      minMSISDN: 0,
      authenticationTypeForm: {
        currentAuthenticationType: '',
        authenticationType: [],
        orgOperatorName: '',
        orgOperatorId: '',
        remark: '',
      },
      authenticationTypeFormRules: {
        authenticationType: [checkRequiredValidateObj],
        remark: [
          { required: true, message: 'Please enter remark', trigger: 'change' },
          { min: 1, max: 512, message: 'Contains 1 to 512 characters.', trigger: 'change' },
        ],
      },
      newRuleProfileOptions: [],
      editRuleProfileVisible: false,
      ruleProfileOptions: [],
      editRuleProfileForm: {
        remark: '',
        ruleProfile: '',
      },
      editRuleProfileRules: {
        ruleProfile: [selectRequiredValidateObj],
        remark: [
          { required: true, message: 'Please enter remark', trigger: 'change' },
          { min: 1, max: 512, message: 'Contains 1 to 512 characters.', trigger: 'change' },
        ],
      },
      newLanguageOptions: [],
      editLanguageVisible: false,
      editLanguageForm: {
        oldLanguage: '',
        languageCode: '',
        remark: '',
      },
      newStatusList: [],
      editLanguageFormRules: {
        remark: [
          { required: true, message: 'Please enter remark', trigger: 'change' },
          { min: 1, max: 512, message: 'Contains 1 to 512 characters.', trigger: 'change' },
        ],
        languageCode: [inputRequiredValidateObj],
      },
      editMSISDNVisible: false,
      editMSISDNForm: {
        receiverIdentityId: '',
        msisdn: '',
        remark: '',
      },
      editMSISDNRules: {
        newMSISDN: [inputRequiredValidateObj],
        msisdn: [{ validator: getMsisdnRule, trigger: 'blur' }],
        remark: [
          { required: true, message: 'Please enter remark', trigger: 'change' },
          { min: 1, max: 512, message: 'Contains 1 to 512 characters.', trigger: 'change' },
        ],
      },
      operationArr: [
        {
          label: 'Reset PIN',
          value: 'resetPIN',
        },
        {
          label: 'Unlock PIN',
          value: 'unlockPIN',
        },
        {
          label: 'Set Password',
          value: 'setPassword',
        },
        {
          label: 'Reset Password',
          value: 'resetPassword',
        },
        {
          label: 'Send SMS',
          value: 'sendSMS',
        },
      ],
      showResetPINDialog: false,
      showUnlockPINDialog: false,
      showResetPasswordDialog: false,
      showSetPasswordDialog: false,
      sendSMSialog: false,
      disabledChannelEnableSubmit: true,
      oldAccessChannel: [],
      resetPinForm: {
        resetPINReason: '',
      },
      resetSMSForm: {
        notificationType: '',
        resetPINReason: '',
      },
      resetPINFormRules: {
        resetPINReason: [
          { required: true, message: 'Please enter remark', trigger: 'change' },
          { min: 1, max: 512, message: 'Contains 1 to 512 characters.', trigger: 'change' },
        ],
      },
      resetPasswordForm: {
        remark: '',
      },
      resetPasswordFormRules: {
        remark: [
          { required: true, message: 'Please enter remark', trigger: 'change' },
          { min: 1, max: 512, message: 'Contains 1 to 512 characters.', trigger: 'change' },
        ],
      },
      unlockPINForm: {
        isResetPIN: true,
        unlockPINReason: '',
      },
      unlockPINFormRules: {
        isResetPIN: [{ required: true, message: 'Please select', trigger: 'change' }],
        unlockPINReason: [
          { required: true, message: 'Please enter reason', trigger: 'change' },
          { min: 1, max: 512, message: 'Contains 1 to 512 characters.', trigger: 'change' },
        ],
      },
      setPasswordForm: {
        password: '',
        confirmPassword: '',
      },
      setPasswordFormRules: {
        password: [{ required: true, message: 'Please enter', trigger: 'change' }],
        confirmPassword: [{ required: true, validator: validateConfirmPassword, trigger: 'change' }],
      },
      receiverIdentityId: '',
      statusOptions: [
        { label: 'All', value: null },
        { label: 'Active', value: '03' },
        { label: 'Closed', value: '06' },
        { label: 'Dormant', value: '07' },
        { label: 'New', value: '00' },
        { label: 'Pending Active', value: '02' },
        { label: 'Suspended', value: '04' },
      ],
      isApiShow: true,
      isUseNameShow: false,
      isOperatorIdShow: false,
      ruleProfileId: '',
      operatorType: '',
      orgOperatorName: '',
      orgOperatorId: '',
      getOperatorID: '',
      getUserName: '',
      organizationStatus: '',
      publicName: '',
      notificationTypeOptions: [],
      receiverOperIdentityId: '',
      isEdit: 'detail',
      smsMap: {},
      operatorDetails: {},
    };
  },
  created() {
    this.receiverOperIdentityId = JSON.parse(decodeURIComponent(this.$route.query.data)).orgOperatorId;
    this.getRuleProfile();
    this.getNotificationData();
  },
  mounted() {
    this.receiverIdentityId = JSON.parse(decodeURIComponent(this.$route.query.data)).orgOperatorId;
    this.getDetail();
    this.orgGetRuleFn();
    this.getOrgOperatorKYCDetail();
  },
  activated() {
    this.loadLanguage();
  },
  deactivated() {
    this.basicInfoForm = {};
  },
  methods: {
    changeAuthenticationType(newVal) {
      this.disabledChannelEnableSubmit =
        this.oldAccessChannel.length === newVal.length &&
        newVal.sort().join('') === this.oldAccessChannel.sort().join('');
    },
    // getRule判断msisdn是否展示
    async orgGetRuleFn() {
      let res = await getMsisdnRule();
      if (
        (res.rules &&
          res.rules.OPERATOR_MSISDN_IDENTIFIER &&
          res.rules.OPERATOR_MSISDN_IDENTIFIER.defaultValue === 'true') ||
        (res.rules && res.rules.OPERATOR_MSISDN_IDENTIFIER && res.rules.OPERATOR_MSISDN_IDENTIFIER.defaultValue === '1')
      ) {
        this.isShowMSISDN = true;
      }
      this.getOperatorID =
        res.rules && res.rules.OPERATOR_ID_VALIDATION_RULE && res.rules.OPERATOR_ID_VALIDATION_RULE.defaultValue;
      this.getUserName =
        res.rules && res.rules.USER_NAME_VALIDATION_RULE && res.rules.USER_NAME_VALIDATION_RULE.defaultValue;
    },
    // 变量接口校验规则
    getRegexpValidateRule(item) {
      if (item) {
        let expression = item;
        let rule = null;
        if (expression) {
          rule = {
            pattern: new RegExp(expression),
            message: `The regular expression is ${expression}.`,
            trigger: 'blur',
          };
        }
        return rule;
      } else {
        return {};
      }
    },
    // rule profile下拉框
    async getRuleProfile() {
      let param = {
        identityType: this.identityType,
      };
      let res = await getRuleProfile(param);
      if (res.header && res.header.success) {
        this.newRuleProfileOptions = res.list || [];
      }
    },
    showRuleProfileName(profileId) {
      let ruleProfileName;
      if (this.newRuleProfileOptions.length > 0) {
        this.newRuleProfileOptions.forEach((item) => {
          if (item.id === profileId) {
            this.ruleProfileId = item.id;
            ruleProfileName = item.ruleProfileName;
          }
        });
        return ruleProfileName;
      }
    },
    goRuleProfile() {
      if (this.basicInfoForm.ruleProfile) {
        let params = {
          id: this.ruleProfileId || this.basicInfoForm.ruleProfile,
        };
        this.openNewWindow({
          path: '/mainPage/identifierCenter/organizationInfo/organization/ruleProfile',
          query: { data: encodeURIComponent(JSON.stringify(params)) },
        });
      }
    },
    showRegisChannel(val) {
      if (!val) {
        return '-';
      }
      if (val === '1005') {
        return 'WEB';
      } else {
        return 'USSD';
      }
    },
    getStatusList(status) {
      let params = {
        statusCode: status,
      };
      this.newStatusList = [];
      getStatusList(params).then((res) => {
        if (res.header && res.header.success) {
          if (res.statusList && res.statusList.length > 0) {
            res.statusList.forEach((item) => {
              this.statusOptions.forEach((item1) => {
                if (item === item1.value) {
                  this.newStatusList.push(item1);
                }
              });
            });
          }
        }
      });
    },
    async getDetail() {
      this.submitLoading = true;
      let res = await orgOperatorDetail({ receiverIdentityId: this.receiverIdentityId });
      if (res.header && res.header.success) {
        if (['02', '03', '04'].includes(res.orgOperatorInfo.status)) {
          this.isEdit = 'edit';
        }
        this.basicInfoForm = res.orgOperatorInfo;
        if (this.basicInfoForm.operatorId) {
          this.authenticationTypeForm.orgOperatorId = this.basicInfoForm.operatorId;
        }
        if (this.basicInfoForm.operatorName) {
          this.authenticationTypeForm.orgOperatorName = this.basicInfoForm.operatorName;
        }
        this.roles = res.roles;
        this.roles.map((ro) => {
          ro.effectiveTime = dateFormat(ro.effectiveTime, 'DD-MM-YYYY');
          ro.expiredTime = dateFormat(ro.expiredTime, 'DD-MM-YYYY');
        });
        this.credentials = res.credentials;
        this.getStatusList(res.orgOperatorInfo.status);
        let isIncludeAPI = res.orgOperatorInfo.accessChannel.includes('1010');
        if (!isIncludeAPI) {
          this.operationArr = this.operationArr.filter((item) => {
            return item.value !== 'setPassword';
          });
        }
        let isIncludeWeb = res.orgOperatorInfo.accessChannel.includes('1005');
        if (!isIncludeWeb) {
          this.operationArr = this.operationArr.filter((item) => {
            return item.value !== 'resetPassword';
          });
        }
        let isIncludeHandset = res.orgOperatorInfo.accessChannel.includes('1007');
        if (!isIncludeHandset) {
          this.operationArr = this.operationArr.filter((item) => {
            return item.value !== 'resetPIN';
          });
        }
        if (isIncludeWeb || isIncludeAPI) {
          this.publicName = this.basicInfoForm.operatorName;
        } else if (!isIncludeWeb && isIncludeHandset) {
          this.publicName = this.basicInfoForm.operatorId;
        } else {
          this.publicName = '-';
        }
        let unLockShow = res.credentials.some(
          (item) => item.credentialStatus === '3' && item.credentialTypeName === '02'
        );
        if (!unLockShow) {
          this.operationArr = this.operationArr.filter((item) => {
            return item.value !== 'unlockPIN';
          });
        }
        // 下拉框展示Send SMS，展示条件参数待定
        if (!this.basicInfoForm.operatorMsisdn) {
          this.operationArr = this.operationArr.filter((item) => {
            return item.value !== 'sendSMS';
          });
        }
      }
      this.getOrganiaztionStatus();
      this.submitLoading = false;
    },
    handleCommand(command) {
      let operationDialogObj = {
        setPassword: 'showSetPasswordDialog',
        resetPassword: 'showResetPasswordDialog',
        unlockPIN: 'showUnlockPINDialog',
        resetPIN: 'showResetPINDialog',
        sendSMS: 'sendSMSialog',
      };
      this[operationDialogObj[command]] = true;
    },
    showStatus(val) {
      return STATUS_MAP[val] || '-';
    },
    // 获取shortCode的状态
    getOrganiaztionStatus() {
      return new Promise((resolve) => {
        let params = {
          shortCode: this.basicInfoForm.shortCode || '',
          pageSize: 5,
          pageNo: 1,
          organizationName: '%',
          status: '',
        };
        queryOrgListByPage(params).then((res) => {
          if (res.header && res.header.success && res.list) {
            this.organizationStatus = res.list[0].status;
            resolve();
          } else {
            resolve('');
          }
        });
      });
    },
    closeResetPasswordDialog() {
      this.$refs['resetPasswordFormRef'].resetFields();
      this.showResetPasswordDialog = false;
    },
    closeResetPINDialog() {
      this.showResetPINDialog = false;
      this.$refs['resetPINFormRef'].resetFields();
    },
    closeUnlockPINDialog() {
      this.showUnlockPINDialog = false;
      this.$refs['unlockPINFormRef'].resetFields();
    },
    closeSetPasswordDialog() {
      this.showSetPasswordDialog = false;
      this.$refs['setPasswordFormRef'].resetFields();
    },
    closeSendSMSialog() {
      this.sendSMSialog = false;
      this.$refs['resetSMSFormRef'].resetFields();
    },

    submitResetPIN() {
      this.$refs['resetPINFormRef'].validate(async (valid) => {
        if (valid) {
          let reqParams = {
            remark: this.resetPinForm.resetPINReason,
            receiverIdentityId: this.receiverIdentityId,
            credentialType: '02',
          };
          this.submitLoading = true;
          let res = await orgOperatorPINRest(reqParams);
          if (res.header && res.header.success) {
            this.showResetPINDialog = false;
            this.getDetail();
            this.$refs['resetPINFormRef'].resetFields();
            if (res.taskId) {
              this.$message.success('The request must be approved by another operator.');
            } else {
              this.$message.success('Reset PIN SUCCESS!');
            }
          }
          this.submitLoading = false;
        }
      });
    },
    submitUnlockPIN() {
      this.$refs['unlockPINFormRef'].validate(async (valid) => {
        if (valid) {
          let reqParams = {
            isResetPIN: this.unlockPINForm.isResetPIN,
            remark: this.unlockPINForm.unlockPINReason,
            receiverIdentityId: this.receiverIdentityId,
            credentialType: '02',
          };
          this.submitLoading = true;
          let res = await orgOperatorPINUnlock(reqParams);
          if (res.header && res.header.success) {
            this.showUnlockPINDialog = false;
            this.getDetail();
            this.$refs['unlockPINFormRef'].resetFields();
            this.$message.success('Operation success.');
          }
          this.submitLoading = false;
        }
      });
    },
    submitSetPassword() {
      this.$refs['setPasswordFormRef'].validate(async (valid) => {
        if (valid) {
          let reqParams = {
            newPassword: this.setPasswordForm.confirmPassword,
            receiverIdentityId: this.receiverIdentityId,
          };
          this.submitLoading = true;
          let res = await orgOperatorPINSetPassword(reqParams);
          if (res.header && res.header.success) {
            this.showSetPasswordDialog = false;
            this.getDetail();
            this.$refs['setPasswordFormRef'].resetFields();
            this.$message.success('Operation success.');
          }
          this.submitLoading = false;
        }
      });
    },
    submitResetPassword() {
      this.$refs['resetPasswordFormRef'].validate(async (valid) => {
        if (valid) {
          let reqParams = {
            remark: this.resetPasswordForm.remark,
            receiverIdentityId: this.receiverIdentityId,
            token: '111',
          };
          this.submitLoading = true;
          let res = await orgResetPassword(reqParams);
          if (res.header && res.header.success) {
            this.showResetPasswordDialog = false;
            this.getDetail();
            this.$refs['resetPasswordFormRef'].resetFields();
            if (res.taskId) {
              this.$message.success('The request must be approved by another operator.');
            } else {
              this.$message.success('Reset Password Success');
            }
          }
          this.submitLoading = false;
        }
      });
    },
    // 获取sms下拉框
    getNotificationData() {
      let data = {
        category: '8',
        scenari: '28',
        pageSize: 100,
      };
      getNotificationTypeList(data).then((res) => {
        if (res.header && res.header.success && res.data.total > 0) {
          this.notificationTypeOptions = res.data.records;
        }
      });
    },
    // 短信下拉框联动
    getNotificationInfo(typeId) {
      getNotificationTypeInfo(typeId).then((res) => {
        if (res.header && res.header.success) {
          const records = res.data.records;
          if (records.length > 0) {
            this.resetSMSForm.resetPINReason = records[0].defaultContent;
            records[0].templateList.forEach((item) => {
              item.entityList.forEach((entityItem) => {
                if (entityItem.language === this.basicInfoForm.language) {
                  this.resetSMSForm.resetPINReason = entityItem.content;
                }
              });
            });
            const kycGroupList = this.operatorDetails.identityKYCDataBean.kycGroupList || [];
            let kycList = kycGroupList.map((item) => {
              return { kycName: item.groupName, kycValue: item.groupId };
            });
            let jsonStr = JSON.stringify(kycList);
            this.resetSMSForm.resetPINReason = this.resetSMSForm.resetPINReason.replace(
              /ManualNotification.KYCList/,
              jsonStr.replace(/:/g, `=`).replace(/"/g, '')
            );
            this.resetSMSForm.resetPINReason = this.resetSMSForm.resetPINReason.replace(
              /ManualNotification.OperatorID/,
              this.basicInfoForm.orgOperatorId
            );
            this.resetSMSForm.resetPINReason = this.resetSMSForm.resetPINReason.replace(
              /ManualNotification.ShortCode/,
              this.basicInfoForm.shortCode
            );
            this.resetSMSForm.resetPINReason = this.resetSMSForm.resetPINReason
              .replace(/ManualNotification.UserName/, this.basicInfoForm.operatorName)
              .replace(/{{|}}/g, '');
            this.smsMap = {
              'ManualNotification.KYCList': kycList,
              'ManualNotification.OperatorID': this.basicInfoForm.orgOperatorId,
              'ManualNotification.ShortCode': this.basicInfoForm.shortCode,
              'ManualNotification.UserName': this.basicInfoForm.operatorName,
            };
          }
        }
      });
    },
    // 获取kycgroup
    async getOrgOperatorKYCDetail() {
      let params = {};
      this.operatorDetails = (await getOrgOperatorKYCDetail(params)) || {};
    },

    getOperatorType(value) {
      this.notificationTypeOptions.forEach((item) => {
        if (item.name === value) {
          this.operatorType = item.name;
        }
      });
    },
    submitSendSMS() {
      this.$refs['resetSMSFormRef'].validate(async (valid) => {
        if (valid) {
          let map = this.resetSMSForm.notificationType ? this.smsMap : { smsContent: this.resetSMSForm.resetPINReason };
          let data = {
            operatorType: 'OrgOperator',
            smsContent: this.resetSMSForm.resetPINReason,
            receiverMsisdn: this.basicInfoForm.operatorMsisdn,
            identityId: this.receiverIdentityId,
            notificationTypeId: this.resetSMSForm.notificationType,
            params: map,
          };
          this.submitLoading = true;
          let res = await sendNotification(data);
          if (res.header && res.header.success) {
            this.sendSMSialog = false;
            this.$refs['resetSMSFormRef'].resetFields();
            this.$message.success('Send SMS Success!');
          }
          this.submitLoading = false;
        }
      });
    },

    closeAuthenticationTypeDialog() {
      this.showAuthenticationTypeDialog = false;
      this.$refs['authenticationFormRef'].resetFields();
    },
    closeEditLanguageDialog() {
      this.editLanguageVisible = false;
      this.$refs['editLanguageFormRef'].resetFields();
    },
    closeProfileDialog() {
      this.editRuleProfileVisible = false;
      this.$refs['editRuleProfileForm'].resetFields();
    },
    closeMSISDNDialog() {
      this.editMSISDNVisible = false;
      this.$refs['editMSISDNForm'].resetFields();
    },
    showAuthenticationType() {
      this.showAuthenticationTypeDialog = true;
      this.oldAccessChannel = JSON.parse(JSON.stringify(this.basicInfoForm.accessChannel || {}));
      this.authenticationTypeForm.authenticationType = this.basicInfoForm.accessChannel;
    },
    handleEditAccessChannel() {
      this.$refs['authenticationFormRef'].validate(async (valid) => {
        if (valid) {
          this.submitLoading = true;
          let params = {
            receiverIdentityId: this.receiverIdentityId,
            accessChannel: this.authenticationTypeForm.authenticationType.toString(),
            remark: this.authenticationTypeForm.remark,
            operatorName: this.authenticationTypeForm.orgOperatorName,
            operatorId: this.authenticationTypeForm.orgOperatorId,
          };
          const accessChannel = this.authenticationTypeForm.authenticationType;
          const hasHandset = accessChannel.includes('1007');
          const hasWeb = accessChannel.includes('1005');
          if (!hasHandset) {
            delete params.operatorId;
          }
          if (!hasWeb) {
            delete params.operatorName;
          }

          let res = await changeAuthentication(params);
          if (res.header && res.header.success) {
            this.showAuthenticationTypeDialog = false;
            if (res.taskId) {
              this.$message.success('The request must be approved by another operator.');
            } else {
              this.$message.success('Change Authentication Type SUCCESS!!');
            }
            this.$refs['authenticationFormRef'].resetFields();
          }
          this.submitLoading = false;
        }
      });
    },
    async handleEditRuleProfile() {
      this.$refs['editRuleProfileForm'].validate(async (valid) => {
        if (valid) {
          let params = {
            receiverIdentityId: this.receiverIdentityId,
            identityRuleProfileID: this.editRuleProfileForm.ruleProfile,
          };
          let res = await changeOrgOperatorRuleProfile(params);
          if (res.header && res.header.success) {
            this.basicInfoForm.ruleProfileName = this.showRuleProfileName(this.editRuleProfileForm.ruleProfile);
            this.editRuleProfileVisible = false;
            this.$message.success('Change Rule Profile Success!');
          }
        }
      });
    },
    editRuleProfileShow() {
      this.editRuleProfileVisible = true;
    },
    async handleEditLanguageShowDialog() {
      let res = await loadLanguageList();
      if (res.header && res.header.success) {
        this.newLanguageOptions = res.list || [];
        this.editLanguageVisible = true;
      }
    },
    async loadLanguage() {
      let res = await loadLanguageList();
      if (res.header && res.header.success) {
        this.newLanguageOptions = res.list || [];
      }
    },
    // 提交修改多语言
    handleEditLanguage() {
      this.$refs['editLanguageFormRef'].validate(async (valid) => {
        if (valid) {
          let reqParams = {
            languageCode: this.editLanguageForm.languageCode,
            remark: this.editLanguageForm.remark,
            receiverIdentityId: this.receiverIdentityId,
          };
          let res = await changeIdentityLanguage(reqParams);
          this.editLanguageVisible = false;
          if (res.header && res.header.success) {
            this.basicInfoForm.language = this.editLanguageForm.languageCode;
            this.$refs['editLanguageFormRef'].resetFields();
            this.$message.success('Change Language success!');
          }
        }
      });
    },
    // 提交修改手机号
    handleEditMSISDN() {
      this.$refs['editMSISDNForm'].validate(async (valid) => {
        if (valid) {
          let reqParams = {
            msisdn: this.editMSISDNForm.msisdn,
            remark: this.editMSISDNForm.remark,
            receiverIdentityId: this.receiverIdentityId,
          };
          let res = await changeMsisdn(reqParams);
          this.editMSISDNVisible = false;
          if (res.header && res.header.success) {
            this.basicInfoForm.operatorMsisdn = this.editMSISDNForm.msisdn;
            this.isShowMSISDN = true;
            this.$refs['editMSISDNForm'].resetFields();
            this.$message.success('Change MSISDN success!');
          }
        }
      });
    },
    getChannelName(channelCodes) {
      let names = [];
      channelCodes.forEach((code) => {
        if (code === '1010') {
          this.isApiShow = false;
        }
        if (code === '1005') {
          this.isUseNameShow = true;
        }
        if (code === '1007') {
          this.isOperatorIdShow = true;
        }
        let item = ACCESS_CHANNEL.find((channelItem) => code === channelItem.value);
        names.push(item ? item.label : code);
      });

      return names.join(',');
    },
    languageFilter(val) {
      let aar = this.newLanguageOptions.filter((item) => {
        return item.languageCode == val;
      });
      if (aar.length > 0) {
        return aar[0].languageName;
      } else {
        return '-';
      }
    },
  },
};
</script>

