<script>
import info from './info';
import AuditLog from './audit-log';
import ReviewReversalRequest from '../../review-reversal-request/list/index.vue';
import ReviewTrasaction from '../../review-transaction/list/index.vue';
import detailPage from '@/mixins/detail-page.vue';

export default {
  name: 'groupDetail',
  components: { info, AuditLog, ReviewReversalRequest, ReviewTrasaction },
  mixins: [detailPage],
  data() {
    return {
      activeName: 'Info', // 对应的label字段
      curComponentName: 'info', // 对应的 component字段
      menuList: [
        {
          name: this.$t('IDC.organizationOperatorDetail.label.Overview'), // list显示的名字
          subMenu: [
            {
              name: this.$t('IDC.organizationOperatorDetail.label.info'),
              label: 'Info',
              component: 'info',
            },
            {
              name: this.$t('IDC.organizationOperatorDetail.label.AuditLog'),
              label: 'AuditLog',
              component: 'audit-log',
            },
          ],
        },
        {
          name: this.$t('IDC.organizationOperatorDetail.label.Transaction'),
          subMenu: [
            {
              name: this.$t('IDC.organizationOperatorDetail.label.ReviewTransaction'),
              label: 'ReviewTransaction',
              component: 'review-trasaction',
            },
            {
              name: this.$t('IDC.organizationOperatorDetail.label.ReviewReversalRequest'),
              label: 'ReviewReversalRequest',
              component: 'review-reversal-request',
            },
          ],
        },
      ],
    };
  },
  methods: {
    handleChange(activeMenu) {
      let { label, component } = activeMenu;
      this.activeMenu = activeMenu;
      this.curComponentName = component;
      if (label) {
        this.activeName = label;
        if (!this.params) {
          this.params = {};
        }
        switch (label) {
          case 'ReviewTransaction':
          case 'ReviewReversalRequest':
            this.params.activeName = 'orgOperator';
            break;
        }
      }
    },
  },
};
</script>
