<template>
  <div>
    <div class="item-desc">{{ $t('IDC.organizationOperatorDetail.RoleDesc') }}</div>
    <el-button v-if="!isViewMode" size="small" icon="el-icon-plus" @click="addBtnFn" type="primary" plain>
      {{ $t('IDC.orgOperator.addRole') }}
    </el-button>
    <el-button v-if="isViewMode" size="small" icon="el-icon-edit" @click="openEditMode" type="primary" plain
      >{{ $t('common.button.edit') }}
    </el-button>
    <el-table
      id="selectedRoleTable"
      size="small"
      :data="isViewMode ? selectedRoleList : tempSelectedList"
      empty-text="No records found."
      class="margin-top-12"
      max-height="450"
    >
      <el-table-column prop="name" :label="$t('IDC.orgOperator.roleName')" min-width="20%" show-overflow-tooltip />
      <el-table-column
        prop="effectiveTime"
        :label="$t('IDC.orgOperator.effectiveDate')"
        min-width="25%"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <span v-if="isViewMode">{{ scope.row.effectiveTime && scope.row.effectiveTime.replace(/-/g, '/') }}</span>
          <el-date-picker
            :id="'effectiveTime' + scope.$index"
            v-else
            size="small"
            style="width: 152px"
            v-model="scope.row.effectiveTime"
            format="dd/MM/yyyy"
            value-format="dd-MM-yyyy"
            type="date"
            :picker-options="pickerOptionsStart(scope.row)"
          />
        </template>
      </el-table-column>
      <el-table-column :label="$t('IDC.orgOperator.expirationDate')" min-width="25%" show-overflow-tooltip>
        <template slot-scope="scope">
          <span v-if="isViewMode">{{ scope.row.expiredTime && scope.row.expiredTime.replace(/-/g, '/') }}</span>
          <el-date-picker
            :id="'expiredTime' + scope.$index"
            v-else
            size="small"
            style="width: 152px"
            v-model="scope.row.expiredTime"
            format="dd-MM-yyyy"
            value-format="dd-MM-yyyy"
            type="date"
            :picker-options="pickerOptionsEnd(scope.row)"
          />
        </template>
      </el-table-column>
      <el-table-column
        prop="description"
        :formatter="descriptionFormatter"
        :label="$t('IDC.orgOperator.description')"
        min-width="20%"
        show-overflow-tooltip
      />
      <el-table-column :label="$t('IDC.orgOperator.operation')" width="150" show-overflow-tooltip>
        <template slot-scope="scope">
          <el-button
            v-if="!isViewMode"
            :id="'delete' + scope.$index"
            size="small"
            type="text"
            class="delete-role-btn"
            @click="deleteRole(scope.$index, scope.row)"
            >{{ $t('common.button.delete') }}</el-button
          >
          <el-button
            v-if="isViewMode"
            :id="'delete' + scope.$index"
            size="small"
            type="text"
            class="delete-role-btn"
            @click="handleDetail(scope.$index, scope.row)"
            >{{ $t('common.button.detail') }}</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div style="height: 24px"></div>
    <el-form
      v-if="!isViewMode"
      ref="editRoleFormRef"
      :model="editRoleForm"
      :rules="editRoleFormRules"
      label-position="top"
      label-width="200px"
    >
      <div class="reset-pin-line"></div>
      <el-form-item prop="remark" label="Remark">
        <el-input v-model.trim="editRoleForm.remark" clearable type="textarea" :rows="4" placeholder="Please enter">
        </el-input>
      </el-form-item>
      <el-form-item class="form-btn">
        <el-button @click="cancelEditMode()" size="small">{{ $t('common.button.cancel') }}</el-button>
        <el-button
          type="primary"
          @click="submitEditRoleForm()"
          size="small"
          :loading="submitLoading"
          v-preventReClick
          >{{ $t('common.button.submit') }}</el-button
        >
      </el-form-item>
    </el-form>
    <el-dialog
      :title="$t('IDC.orgOperator.roleName')"
      :visible.sync="searchRoleVisible"
      width="800px"
      :close-on-click-modal="false"
      :destroy-on-close="true"
    >
      <table-page
        v-if="searchRoleVisible"
        ref="roleDialogTable"
        idKey="roleCode"
        size="small"
        :height="320"
        :isCheck="true"
        :isCheckMemory="false"
        :isSingleCheck="false"
        :has-pagination="false"
        :pagination="paginationConfig"
        :columns="roleColumns"
        :initChecks="displayRoleList"
        :hasPagination="false"
        :getTableData="getRoleTableData"
        @selection-change="handleRoleSelection"
      >
      </table-page>
      <div slot="footer">
        <el-button id="cancelSearchRole" size="small" @click="searchRoleVisible = false">{{
          $t('common.button.cancel')
        }}</el-button>
        <el-button id="ok" type="primary" size="small" @click="addRole" v-preventReClick>{{
          $t('common.button.confirm')
        }}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { queryOrgOprRoleList, changeOrgOperatorRole } from '../../../../api/api-organization-operator';
import TablePage from '@/components/tablePage';
import { formatStrToDate2, dateFormat } from '@/common/utils/date-time-utils';
import { inputRequiredValidateObj, len64StrValidateObj } from '@/common/validator/common';

export default {
  name: 'AssignRole',
  components: { TablePage },
  props: {
    selectedRoleList: {
      type: Array,
      default: () => [],
    },
    identityID: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      isViewMode: true,
      searchRoleVisible: false,
      availableRoleList: [],
      submitLoading: false,
      roleItems: [],
      roleColumns: [
        { label: this.$t('IDC.orgOperator.roleName'), prop: 'roleName', align: 'left' },
        { label: this.$t('IDC.orgOperator.description'), prop: 'description', align: 'left' },
      ],
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 5,
      },
      displayRoleList: [],
      tempSelectedList: [],
      editRuleProfileVisible: false,
      editRoleForm: {},
      editRoleFormRules: {
        remark: [inputRequiredValidateObj, len64StrValidateObj],
      },
    };
  },
  watch: {
    selectedRoleList: {
      handler(val) {
        this.tempSelectedList = [...val];
        this.displayRoleList = [...val];
      },
    },
  },
  created() {
    this.tempSelectedList = [...this.selectedRoleList];
    this.roleItems = [...this.selectedRoleList];
  },
  methods: {
    addBtnFn() {
      this.searchRoleVisible = true;
    },
    openEditMode() {
      this.isViewMode = false;
    },
    cancelEditMode() {
      this.editRoleForm.remark = '';
      this.isViewMode = true;
    },
    getRoleTableData() {
      let queryParams = {};
      queryParams.roleType = '30';
      return new Promise((resolve) => {
        queryOrgOprRoleList(queryParams)
          .then((res) => {
            if (res.header && res.header.success) {
              this.availableRoleList = res.grantRoleList || [];
              let tempRes = {
                header: res.header,
                list: this.availableRoleList,
              };
              resolve(tempRes);
            }
          })
          .catch(() => {
            resolve();
          });
      });
    },
    pickerOptionsStart(row = {}) {
      return {
        disabledDate(time) {
          if (row.expiredTime) {
            if (time.getTime() > formatStrToDate2(row.expiredTime).getTime()) {
              return true;
            }
            if (time.getTime() < Date.now() - 8.64e7) {
              return true;
            }
          } else {
            if (time.getTime() < Date.now() - 8.64e7) {
              return true;
            }
          }
        },
      };
    },
    pickerOptionsEnd(row = {}) {
      return {
        disabledDate(time) {
          if (row.effectiveTime) {
            return time.getTime() > Date.now || time.getTime() < formatStrToDate2(row.effectiveTime).getTime();
          } else {
            return time.getTime() <= Date.now();
          }
        },
      };
    },
    deleteRole(index, row) {
      let tip = this.$t('IDC.orgOperator.deleteTip', { obj: row.name });
      this.$confirm(tip, this.$t('common.button.delete'), {
        confirmButtonText: this.$t('common.button.confirm'),
        cancelButtonText: this.$t('common.button.cancel'),
        closeOnClickModal: false,
        customClass: 'delete-confirm',
        type: 'warning',
      })
        .then(() => {
          let roleItem = this.tempSelectedList.find((item) => item.roleId === row.roleId);
          this.tempSelectedList.splice(index, 1);
          this.$emit('selectedRoleList', this.tempSelectedList);
          this.availableRoleList.push(roleItem);
        })
        .catch(() => {})
        .finally(() => {});
    },
    handleDetail(index, row) {
      let params = {
        ...row,
      };
      let path = '';
      path = '/mainPage/identifierCenter/organizationInfo/orgOperator/roleDetail';
      this.openNewWindow({ path: path, query: { data: encodeURIComponent(JSON.stringify(params)) } });
    },
    handleRoleSelection(val) {
      this.roleItems = val;
      if (val.length > 1) {
        let selectedRoleIdList = [];
        this.roleItems.forEach((item) => selectedRoleIdList.push(item.roleCode));
        let param = {};
        param.targetRoleList = selectedRoleIdList;
      }
    },
    deleteRoleDouble() {
      if (this.tempSelectedList.length > this.roleItems.length) {
        this.tempSelectedList.forEach((role, index) => {
          let roleItem = this.roleItems.find((item) => item.roleId === role.roleId);
          if (!roleItem) {
            this.tempSelectedList.splice(index, 1);
            this.deleteRoleDouble();
          }
        });
      }
    },
    addRole() {
      this.displayRoleList = [];
      this.roleItems.forEach((role) => {
        let index = this.availableRoleList.findIndex((item) => Object.is(item.roleId, role.roleId));
        let roleItem = this.availableRoleList[index];
        if (roleItem) {
          this.$set(roleItem, 'effectiveTime', dateFormat(new Date().getTime(), 'DD-MM-YYYY'));
          this.$set(roleItem, 'expiredTime', '31-12-2099');
          roleItem.roleId = roleItem.roleCode;
          roleItem.name = roleItem.roleName || '-';
          roleItem.description = roleItem.description || '-';
          let roleSelected;
          if (this.tempSelectedList.length > 0) {
            roleSelected = this.tempSelectedList.find((item) => {
              return item.roleId === roleItem.roleId;
            });
            if (!roleSelected) {
              this.tempSelectedList.push(roleItem);
            }
          } else {
            this.tempSelectedList.push(roleItem);
          }
          // 更新可选列表
          this.availableRoleList.splice(index, 1);
        }
      });
      this.displayRoleList = this.tempSelectedList;
      this.$emit('selectedRoleList', this.tempSelectedList);
      this.searchRoleVisible = false;
    },
    validate() {
      let flag = true;
      if (this.tempSelectedList.length === 0) {
        flag = false;
      }
      return flag;
    },
    submitEditRoleForm() {
      this.submitLoading = true;
      this.$refs['editRoleFormRef'].validate(async (valid) => {
        if (valid) {
          let roles = [];
          this.tempSelectedList.map((li) => {
            let rolesList = {};
            let a = li.effectiveTime.split('-');
            let newEffectiveTime = a.reverse().join('-');
            let c = li.expiredTime.split('-');
            let newExpiredTime = c.reverse().join('-');
            rolesList.roleId = li.roleCode || li.roleId;
            rolesList.name = li.name;
            rolesList.roleCode = li.roleCode;
            rolesList.effectiveTime = newEffectiveTime;
            rolesList.expiredTime = newExpiredTime;
            rolesList.status = li.status;
            roles.push(rolesList);
          });
          let reqParams = {
            roles: roles,
            remark: this.editRoleForm.remark,
            receiverIdentityId: this.identityID,
          };
          let res = await changeOrgOperatorRole(reqParams);
          if (res.header && res.header.success) {
            this.editRoleForm.remark = '';
            this.$refs['editRoleFormRef'].resetFields();
            this.$message.success('Operation success.');
            this.isViewMode = true;
            this.$emit('getDetail');
          }
          this.submitLoading = false;
        } else {
          this.submitLoading = false;
        }
      });
    },
    descriptionFormatter(row) {
      return row.description || '-';
    },
  },
  computed: {},
  mounted() {},
};
</script>

<style lang="scss" scoped>
.item-desc {
  color: $--line_color--strong;
  font-size: 12px;
  margin-bottom: 20px;
}
.form-btn {
  text-align: right;
  margin-bottom: 20px;
}
.role-configuration {
  font-size: 14px;
  color: $--text_color--emphasis;
  font-weight: 700;
  margin-bottom: 16px;
}
.delete-role-btn {
  text-align: left;
}
.dialog-sub-title {
  width: 292px;
  height: 15px;
  font-size: 12px;
  color: $--line_color--strong;
  font-weight: 400;
  margin-bottom: 20px;
}
.search-btn-container {
  display: flex;
  justify-content: flex-end;
  margin-top: 32px;
}
</style>
